import React , {useState , useEffect} from 'react'
import {Link} from 'react-scroll';

function Banner() {


var i = 0;
var txt1 = 'Welcome to ZeekoTech '; /* The text */
var speed = 50; /* The speed/duration of the effect in milliseconds */

const  typeWriter = () => {
 

  if (i < txt1.length) {
    document.getElementById("banner-text").innerHTML += txt1.charAt(i);
    i++;    

    setTimeout(typeWriter, speed);
  }


} 

useEffect(() => {
  typeWriter();
}, [])



    return (
        <div>





<div class="container-fluid   banner  "   >
  <div class="container">
    <div class="row text-center  bg-content-row  p-0" style={{"position":'relative', 'marginBottom':'100px' ,'height':'400px'}} >
   

      <div class="col  " >
        <h1 class="text-white " style={{'marginTop':'30px'}} id="banner-text" ></h1>
         <Link to='contact'   smooth={true}   class="btn btn-outline-success btn-lg  text-white"> Get Started </Link>
      </div>

     
        

    </div>
  </div>

</div> 

            
        </div>
    )
}

export default Banner
