import React from 'react';

import {Link} from 'react-scroll';

// const Link = Scroll.Link;


function Nav() {


    const gotoScrollTop = (e) => {
        e.preventDefault();
    
        window.scrollTo({
            top:0,
            behavior : 'smooth'
        })

        w3_close();
      }



    
    const  w3_open = () => {
        document.getElementById("mySidebar").style.display = "block";
        document.getElementById("myOverlay").style.display = "block";
    }
    const  w3_close = () =>  {
        document.getElementById("mySidebar").style.display = "none";
        document.getElementById("myOverlay").style.display = "none";
    }




    return (
        <div>



{/* <!-- Sidebar --> */}
<div class="w3-sidebar w3-bar-block w3-white w3-animate-left d-lg-none " style={{'display':'none','zIndex':'20'}} id="mySidebar">
  <button class="w3-bar-item w3-button w3-red" onClick={w3_close}>Close &times; </button>
  <Link to='home'  onClick={gotoScrollTop}  class="w3-bar-item w3-button "> <i class="fas fa-home"></i> Home </Link>
  <Link to='services'   smooth={true} onClick={w3_close}   class="w3-bar-item w3-button" style={{'textAlign':'left'}}> <i class="fab fa-servicestack"></i>  Services</Link>
  <Link to='whyus'   smooth={true} onClick={w3_close} class="w3-bar-item w3-button "> <i class="fab fa-servicestack"></i>  Why Us</Link>
  <Link to='contact'   smooth={true} onClick={w3_close}  class="w3-bar-item w3-button w3-yellow w3-hover-green"  >  <i class="fas fa-id-card"></i> Contact </Link>
</div>
<div class="w3-overlay d-lg-none    " onClick={w3_close} style={{'cursor':'pointer', 'zIndex':'15'}}  id="myOverlay" ></div>
{/* side bar ends */}






  <nav class="container-fluid  navbar navbar-expand-lg   "  id="main-topnav" style={{"position": "fixed" ,  "zIndex":"10"}} >

      
      <div class="container "  style={{"backgroundColor": "transparent"}} >
        <Link to='home'  onClick={gotoScrollTop}    class="navbar-brand mr-5 w3-text-white  "  style={{"fontWeight":"bolder", "fontSize":'30px' , 'cursor':'pointer'}} >ZeekoTech</Link>
        <button
          class="navbar-toggler d-block d-lg-none w3-green"
          type="button"
          data-mdb-toggle="collapse"
          data-mdb-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"

          onClick={w3_open}
        >
          <i class="fas fa-bars" ></i>
        </button>

        


        <div class="collapse navbar-collapse  d-none d-lg-block " id="navbarNav">
          <ul class="navbar-nav ml-auto ">
            <li class="nav-item ">
              <Link to='/'  onClick={gotoScrollTop}  smooth={true}  class="nav-link active w3-text-white w3-hover-green" style={{"fontSize":"20px" , 'cursor':'pointer'}} aria-current="page" > <i class="fas fa-home"></i>  Home</Link>
            </li>

            <li class="nav-item p-0">
              {/* <Link  to='services'  spy={true} smooth={true}   class="nav-link active w3-text-white w3-hover-green" style={{"fontSize": "20px"}} > <i class="fab fa-servicestack"></i>  Services </Link> */}
              <Link  to='services'    smooth={true}   class="nav-link active w3-text-white w3-hover-green  " style={{"fontSize": "20px" , 'cursor':'pointer'}} > <i class="fab fa-servicestack"></i>  Services </Link>
            </li>

            <li class="nav-item">
              <Link  to='whyus'   smooth={true}   class="nav-link w3-text-white w3-hover-green" style={{"fontSize": "20px" , 'cursor':'pointer'}} > <i class="fab fa-servicestack"></i>  Why Us </Link>
            </li>

            
               
            <li class="nav-item w3-yellow   w3-hover-red mx-2 " style={{"padding":'0.5px','fontSize':'20px' , 'cursor':'pointer' }} >
              <Link to='contact'   smooth={true}  class="nav-link text-black   " > <i class="fas fa-id-card"></i> Contact Us </Link>
            </li>



          </ul>
        </div>
      </div>
    </nav>



        </div>
    )
}

export default Nav;
