import axios from 'axios';
import React, {useState } from 'react'

const END_POINT = '127.0.0.1:8000'
// const END_POINT = '3.121.228.154:8000'

function Contact() {

    const [contact , setContact ] = useState({'name':'' , 'email':'' , 'subject':'' , 'message':''});
    const [loading , setLoading] = useState(false);
     const [sucResponse , setSucResponse] = useState("");
     const [failResponse , setFailResponse] = useState("");



    const handleChange = (e) => {
        const name = e.target.name;
        const value = e.target.value;

        setContact({...contact , [name] : value })

    }

    const handleSubmit = (e) => {
        e.preventDefault();
        
        // sent the contact to business email for further processing

        setLoading(true);
        
       axios
       .post(`api/contact/` , contact  , {'content-type':'application/json'})
      //  .post(`${END_POINT}/api/contact/` , contact  , {'content-type':'application/json'})
        .then(res => {

          setSucResponse(res.data.message);
          setFailResponse("")          
          setLoading(false);

          console.log(res.data.message)

        })
        .catch(e => {

          // console.log(e.response)

          setFailResponse("Unable to send mail at the moment, try again...")
          setSucResponse('');
          setLoading(false);

          
        });
        
    }


    return (
        <div>

<section id="contact" className="section-bg wow fadeInUp py-5">
      <div className="container">

        <div className="section-header">
          <h3 className="" style={{'textTransform':'capitalize'}} >Contact Us</h3>
          <p>Lets hear from you...</p>
        </div>

        <div className="row contact-info">

          <div className="col-md-4">
            <div className="contact-address">
              <i className="ion-ios-location-outline"></i>
              <h3 style={{'textTransform':'capitalize'}} >Address</h3>
              <address> 10B Kingsley-Emu Street, Lekki Phase 1, Lagos </address>
            </div>
          </div>

          <div className="col-md-4">
            <div className="contact-phone">
              <i className="ion-ios-telephone-outline"></i>
              <h3 style={{'textTransform': 'capitalize'}} >Phone Number</h3>
              <p><a href="tel:+155895548855">+2347014357400</a></p>
            </div>
          </div>

          <div className="col-md-4">
            <div className="contact-email">
              <i className="ion-ios-email-outline"></i>
              <h3 style={{'textTransform': 'capitalize'}} >Email</h3>
              <p><a href="mailto:info@example.com">info@example.com</a></p>
            </div>
          </div>

        </div>





        <div className="form" >
          <div id="sendmessage">Your message has been sent. Thank you!</div>
          <div id="errormessage"></div>

          {/* <h5 className="text-info"> {loading ? "Loading..." : "" } </h5> */}
          <h5 className="text-danger"> { failResponse ? failResponse : "" } </h5>
          <h5 className="text-success">  { sucResponse ? sucResponse : "" } </h5>


          <form  onSubmit={handleSubmit}>
            <div className="form-row">
              <div className="form-group col-md-6">
                <input type="text" name="name"  required className="form-control" id="name" onChange={handleChange} placeholder="Your Name" data-rule="minlen:4" data-msg="Please enter at least 4 chars" />
                <div className="validation"></div>
              </div>
              <div className="form-group col-md-6">
                <input type="email" className="form-control" required name="email" id="email" onChange={handleChange} placeholder="Your Email" data-rule="email" data-msg="Please enter a valid email" />
                <div className="validation"></div>
              </div>
            </div>
            <div className="form-group">
              <input type="text" className="form-control" required name="subject" id="subject" onChange={handleChange}  placeholder="Subject" data-rule="minlen:4" data-msg="Please enter at least 8 chars of subject" />
              <div className="validation"></div>
            </div>
            <div className="form-group">
              <textarea className="form-control" name="message" required rows="5" data-rule="required" data-msg="Please write something for us" onChange={handleChange} placeholder="Message"></textarea>
              <div className="validation"></div>
            </div>
            
            <div className="text-center">
              {loading ? 

                <input type="submit" value="Sending..."  disabled className="btn w3-grey" />     

                :

                <input type="submit" value="Send Mesaage" className="btn btn-success" />  

                

            }
                

                </div>
          </form>
        </div>

      </div>
    </section>
    


            
        </div>
    )
}

export default Contact
