import axios from 'axios';
import React , {useState} from 'react'
import {Link}  from 'react-scroll';


// const END_POINT = '3.121.228.154:8000'
const END_POINT = "127.0.0.1:8000"


const  Footer = () =>  {
     const  [ email  , setEmail  ] = useState('');
     const [loading , setLoading] = useState(false);
     const [sucResponse , setSucResponse] = useState("");
     const [failResponse , setFailResponse] = useState("");

     const handleSubmit = (e) => {
         e.preventDefault();

        //  send the email to the db using axios
        // perform axios post to subscription db
      

        setLoading(true)
        axios
        .post(`api/subscribe/` , {'email':email} , {'content-type':'application/json'})
        // .post(`${END_POINT}/api/subscribe/` , {'email':email} , {'content-type':'application/json'})
        .then((res) => {

          setSucResponse("Subscription Successful");
          setFailResponse("")          
          setLoading(false);

          console.log(res.data)
        })
        .catch(e => {          
          console.log(e.response.data.email[0])

          setFailResponse(e.response.data.email[0])
          setSucResponse('');
          setLoading(false);

        });

     }


    //  scroll back to top from footer
     const gotoScrollTop = (e) => {
      e.preventDefault();
  
      window.scrollTo({
          top:0,
          behavior : 'smooth'
      })

     
    }


    return (
        <div>
            

    <footer id="footer">
    <div className="footer-top ">
      <div className="container">
        <div className="row text-left">

        <div className="col-lg-3 col-md-6 " >
          <h2 className="w3-text-white" style={{'fontWeight': 'bolder', 'cursor':'pointer'}} >  <Link to="main-topnav"  smooth={true}  onClick={gotoScrollTop} >  ZeekoTech  </Link> </h2>
          </div>

          <div className="col-lg-3 col-md-6 footer-links">
            <h4>Useful Links</h4>
            <ul>
              <li><i className="ion-ios-arrow-right"></i> <Link to='main-topnav' onClick={gotoScrollTop}  smooth={true}  href="#">Home</Link></li>
              <li><i className="ion-ios-arrow-right"></i> <Link to='services'  smooth={true} href="#" >Services</Link></li>
              <li><i className="ion-ios-arrow-right"></i> <Link to='whyus'  smooth={true} href="#" >About us</Link></li>
               {/* <li><i className="ion-ios-arrow-right"></i> <Link to='terms'  smooth={true}  >Terms of service</a></li>  */}
              <li><i className="ion-ios-arrow-right"></i> <Link to='contact'  smooth={true} href="#"> Contact </Link></li>
            </ul>
          </div>


          <div className="col-lg-3 col-md-6 footer-contact">
            <h4>Contact Us</h4>
            <p>
              10B Kingsley-Emu Street <br/>
              Lekki Phase 1, <br/>
              Lagos State. <br/>
              <strong>Phone:</strong> +234701 435 7400<br/>
              <strong>Email:</strong> info@example.com<br/>
            </p>
            
            <div className="social-links">
              <a href="#" className="twitter"><i className="fa fa-twitter"></i></a>
              <a href="#" className="facebook"><i className="fa fa-facebook"></i></a>
              <a href="#" className="instagram"><i className="fa fa-instagram"></i></a>
              <a href="#" className="google-plus"><i className="fa fa-google-plus"></i></a>
              <a href="#" className="linkedin"><i className="fa fa-linkedin"></i></a>
            </div>
          </div>


        
          
          <div className="col-lg-3 col-md-6 footer-newsletter">
            <h4>Our Newsletter</h4>
            <p>Subscribe to our Newsletter for latest updates</p>
            <h5 className="text-info"> {loading ? "Loading..." : "" } </h5>
            <h5 className="text-danger"> { failResponse ? failResponse : "" } </h5>
            <h5 className="text-success">  { sucResponse ? sucResponse : "" } </h5>

            <form  onSubmit={handleSubmit} >
              <input type="email" name="email" onChange={ e => setEmail(e.target.value)} />
              <input type="submit"  value="Subscribe" />
            </form>
          </div>



        </div>
      </div>
    </div>

    <div className="container">
      <div className="copyright">
        &copy; Copyright <strong>ZeekoTech</strong> . 
      </div>
    </div>
  </footer>
  

  <a href="#" className="back-to-top"><i className="fa fa-chevron-up"></i></a>


        </div>
    )
}

export default Footer
